body {
  margin: 0;
  padding: 0;
}

.wrapper {
  background: url(/clicky-game/static/media/background.1cf4ecc7.jpg);
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  min-height: 100vh;
  padding: 0% 10%;
}

.animalWrapper {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
    min-height: 100vh;
    padding: 10%;
    width: 100%;
    font-size: 1.7rem;
    color: white;
    font-family: 'Shadows Into Light Two';
    text-shadow: 1px 2px #464646;
  }
  
.card {
  border-radius: 2px;
  height: 175px;
  margin: 7px 2px;
  position: relative;
  width: 175px;
  background-color: white;
  padding: 10px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  cursor: pointer;
}

.card > img {
  height: 100%;
  width: 100%
}



.nav {
  width: 100%;
  font-size: 3rem;
  color: white;
  font-family: 'Amatic SC', cursive;
}

